.tarif.site {
  padding-bottom: 128px;
  h2 {
    padding-bottom: 39px; }
  .tarif__item {
    padding: 26px 32px;
    .title {
      text-align: center;
      padding-bottom: 40px; } }
  .radioblock {
    position: relative;
    input {
      opacity: 0;
      position: absolute;
      left: 0;
      right: 0; }
    label {
      line-height: 27px;
      display: block;
      height: 27px;
      padding-left: 40px;
      font-size: 13px;
      color: #c43939;
      position: relative;
      cursor: pointer;
      margin: 0; }
    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 27px;
      height: 27px;
      border-width: 1px;
      border-color: rgb(213, 226, 233);
      border-style: solid;
      border-radius: 50%;
      background-color: rgb(246, 246, 246); }
    &:after {
      content: '';
      display: block;
      background-color: rgb(213, 226, 233);
      border-radius: 50%;
      width: 15px;
      height: 15px;
      position: absolute;
      left: 6px;
      top: 6px; } }
  .price-order {
    text-align: center;
    display: block;
    padding-right: 0;
    .price {
      padding-left: 0; }
    .order {
      margin-left: auto;
      margin-right: auto; } } }

